<template>
  <div
    class="lyb-grid-custom"
    :style="{
      gridTemplateRows: rows,
      gridTemplateColumns: columns,
      gridGap: gap,
    }"
  >
    <slot></slot>
  </div>
</template>
<script>
export default {
  props: {
    // 格子之间的间隔
    gap: {
      type: String,
      default: "",
    },
    /* 以下两个属性是针对格子宽高不固定 */
    //相当于 grid 的 grid-template-columns
    columns: {
      type: String,
      default: "",
    },
    //相当于 grid 的 grid-template-rows
    rows: {
      type: String,
      default: "",
    },
  },
  name: "lyb-grid-custom",
};
</script>
<style scoped lang="less">
.lyb-grid-custom {
  width: 100%;
  height: 100%;
  display: grid;
}
</style>
